.SettingsTab {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	color: white;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
	overflow: hidden;
}

.SettingsTabButton {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	background: none;
	color: white;
	font-size: 1.2em;
	font-weight: 500;
	width: 100%;
	padding: 16px 0;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}

.SettingsTabButton:hover, .SettingsTabButtonActive {
	background: var(--main-shade);
	cursor: pointer;
	border-color: white !important;
}


