.activeUserContainer {
	width: 100%;
	padding: 16px 0px;
	color: #fff;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	text-transform: capitalize;
	font-size: 1.2em;
	font-weight: 500;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}

.activeUserContainer:hover {
	cursor: pointer;
	background: var(--main-shade);
}

.activeUser {
	display: inline;
	white-space: nowrap;
}


