*, *:before, *:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body, html {
  height: 100%;
  background: var(--main-color);
}

body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 16px;
}

#root {
  height: 100%;
}

.App {
  height: 100%;
}

.page {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
 margin: 0;
}

a, input {
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s
}

a {
  color:  var(--main-color);
}

a:hover {
  color: rgba(117,103,167,1) !important;
  text-decoration: underline;
}

button {
  min-height: 1em;
}

.mainButton, .secondaryButton {
  width: 100%;
  padding: 0.75em 1em;
  font-size: 1.1em;
  font-weight: 300;
  color: white;
}

.secondaryButton {
  border: 2px solid #fff;
  color: #fff;
  background: none;
  font-weight: bold;
}

input {
	border: 1px solid rgba(34, 36, 38, 0.15);
	border-radius: 0.3em;
	font-size: 1em;
  line-height: 1.2em;
}

input:hover, a {
  cursor: pointer;
}

input[type='button']:hover, input[type='submit']:hover {
  background:  var(--main-shade);
  border-color: var(--main-shade);
}

input[type='text']:hover, input[type='password']:hover {
  cursor: text;
}

input:focus {
  outline: none;
}


input[type='password']:focus, input[type='text']:focus {
	-webkit-box-shadow: inset 0 1px 2px rgba(255,85,85,0.2);
	        box-shadow: inset 0 1px 2px rgba(255,85,85,0.2);
	border-color: rgba(255, 85, 85, 1);
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes icon-bob {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  25% {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
  }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  75% {
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes icon-bob {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  25% {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
  }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  75% {
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);    
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);    
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    width: 0%;
    opacity: 0;
  }
  100% {
    width: 100%;
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    width: 0%;
    opacity: 0;
  }
  100% {
    width: 100%;
    opacity: 1;
  }
}

:root {
  --main-color: rgba(255, 85, 85, 1);
  --main-shade: rgba(229, 76, 76, 1);
}



