.Menu {
	position: relative;
	margin: 16px 0px;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}

.MenuHeader {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	background:  var(--main-shade);
	height: 100px;
	width: calc(100% + 4px);
	-webkit-box-shadow: 0px 4px 10px rgba(51,51,51, 0.6);
	        box-shadow: 0px 4px 10px rgba(51,51,51, 0.6);
	font-size: 2.4em;
}

.SettingsTabList {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	margin: 16px 0;
}



